@import "../../bootstrap5/functions";
@import "../../bootstrap5/variables"; 
@import "../../bootstrap5/mixins";

@import "variables";

// tab

.cathay-carosel{
  &.#{$prefix}-layout-tab{
    // padding-top: 25px;
    
    .slick-slider{
      // border-bottom:1px solid #1d2020;
      margin-left: auto;
      margin-right: auto;
      width: 100%;
      @media (min-width: map-get($grid-breakpoints, 'md')){
        // width: 50%;
      }

      @media (min-width: map-get($grid-breakpoints, 'lg')){
        max-width: 960px;
      }

    }
    .slick-list{
      margin-bottom: -1px;
    }
    .slick-slide{
      position: relative;
      cursor: pointer;


      &:hover{
        color:#00a94f;
        cursor: pointer;
      }
    }

    .#{$prefix}img-wrap{
      display: none;
    }
    .#{$prefix}txt-wrap{
      display: block;
      border-bottom: 3px solid transparent;
      border-right: none;
      border-left: none;
      border-top: none;
      position: relative;
      color: #333333;
      opacity: 1;
    }
    
    .#{$prefix}title{
      text-align: center;
      font-size:18px;
      line-height: 2;
      letter-spacing: 3px;
      font-weight:400;

      &:before{
        position: absolute;
        content: '';
        display: block;
        bottom: -2px;
        left: 0;
        width: 100%;
        height: 1px;
        background-color: #1d2020;
      }
      &:after{
        position: absolute;
        content: '';
        display: block;
        bottom: -3px;
        left: 0;
        width: 0%;
        height: 1px;
        background-color: #00a94f;
        transition: width .25s;
      }
    }

    .slick-current{
      .#{$prefix}title{
        color:#00a94f;

        &:after{
          height: 4px;
          width: 100%;
        }
      }

    }
    .#{$prefix}subtitle{
      display: none;
    }
    .#{$prefix}link{
      // position: absolute;
      // top:0;
      // left:0;
      // width: 100%;
      // height: 100%;
      // opacity:.1;
    }

    .#{$prefix}content{
      display: none;
    }

    .#{$prefix}more{
      display: none;
    }

    
    .slick-dots{
      bottom:0px;
    }



    /* Arrows */
    .slick-prev,.slick-next{
        position: absolute;
        display: block;
        width: 40px;
        height: 40px;
        border: 0;//2px solid $slick-arrows-color;
        border-radius: 50%;
        background-color: transparent;
        line-height: 0px;
        font-size: 0px;
        cursor: pointer;
        outline: none;
        top: calc(50% - 22px);

        transition: all .15s ease-in-out;

        &:before {
            content:"";
            display: inline-block;
            margin-top:2px;
            width: 10px;
            height: 10px;
            border-top: 2px solid #666666;
            border-right: 2px solid #666666;
        }
        &:hover{
            outline: none;
            transform: scale(1.1);
        }
        &.slick-disabled{
            opacity: $slick-opacity-not-active;
        }
        &.slick-disabled:before {
            opacity: $slick-opacity-not-active;
        }
    }


    .slick-prev {
        left: 0px;
        z-index: 100;
        &:before {
            margin-left: 3px;
            -moz-transform: rotate(-135deg);
            -webkit-transform: rotate(-135deg);
            transform: rotate(-135deg);
        }
    }

    .slick-next {
        right: 0px;
        z-index: 101;
        &:before {
            margin-left: -3px;
            -moz-transform: rotate(45deg);
            -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
        }
    }
  }
}