@charset "UTF-8";

$prefix:'cs-';
$screen-sm:480px;
$color-green:#00a94f !default;
$slick-loader-path: "./" !default;
$slick-arrow-color: white !default;
$slick-dot-color: #666666 !default;
$slick-dot-border-color: white !default;
$slick-dot-color-hover: $color-green !default;
$slick-dot-border-color-hover: white !default;

$slick-dot-color-active: $color-green !default;
$slick-dot-border-color-active: $color-green !default;

$slick-dot-border-hover: white !default;
$slick-dot-border: $color-green !default;
$slick-arrows-color:$color-green !default;

$slick-dot-size: 16px !default;
$slick-opacity-default: 0.75 !default;
$slick-opacity-on-hover: 1 !default;
$slick-opacity-not-active: 0.25 !default;

$section-wrap-bg-color:rgba(19,144,71,1) !default;
$section-wrap-bg-color-hover:rgba(23,87,49,1) !default;
$section-wrap-bg-color-active:rgba(23,87,49,1) !default;

@function slick-image-url($url) {
    @if function-exists(image-url) {
        @return image-url($url);
    }
    @else {
        @return url($slick-loader-path + $url);
    }
}

@function slick-font-url($url) {
    @if function-exists(font-url) {
        @return font-url($url);
    }
    @else {
        @return url($slick-font-path + $url);
    }
}